#rain-container {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

@keyframes rain {
  0% {
    transform: translate(var(--x-start), var(--y-start)) rotate(0);
  }
  100% {
    transform: translate(var(--x-end), var(--y-end)) rotate(50deg);
  }
}

.rainDrop {
  font-size: var(--sz);
  filter: blur(var(--blur));
  animation: rain 30s linear infinite;
  transform: translate(var(--x-start), -15vh) rotate(0);
  opacity: var(--a);
}

.rainDrop--s {
  --sz: 1rem;
  --blur: 2px;
  --a: 1;
  --x-start: 50vw;
  --x-end: 0;
  --y-start: 0;
  --y-end: 100vh;
  position: absolute;
}

.rainDrop--m {
  --sz: 2rem;
  --blur: 2px;
  --a: 0.8;
  --x-start: 50vw;
  --x-end: 0;
  --y-start: 0;
  --y-end: 100vh;
  position: absolute;
  filter: blur(0px);
  z-index: 50;
}

.rainDrop--l {
  --sz: 3rem;
  --blur: 0px;
  --a: 0.8;
  --x-start: 50vw;
  --x-end: 0;
  --y-start: 0;
  --y-end: 100vh;
  position: absolute;
  z-index: 80;
}

.rainDrop--xl {
  --sz: 6rem;
  --blur: 1;
  --a: 0.5;
  --x-start: 50vw;
  --x-end: 0;
  --y-start: 0;
  --y-end: 100vh;
  position: absolute;
  z-index: 100;
}
